// ======= Start Media Query mixin ===== //

@mixin breakpoint($point) {
  @if $point ==small {
    @media (max-width: 575px) {
      @content;
    }
  }

  @else if $point ==phone {
    @media (min-width: 576px) {
      @content;
    }
  }

  @else if $point ==mobile {
    @media (max-width: 767px) {
      @content;
    }
  }

  @else if $point ==tablet {
    @media (min-width: 768px) {
      @content;
    }
  }

  @else if $point ==laptop {
    @media (min-width: 992px) {
      @content;
    }
  }

  @else if $point ==desktop {
    @media (min-width: 1200px) {
      @content;
    }
  }

  @else if $point ==tv {
    @media (min-width: 1400px) {
      @content;
    }
  }
}

// ======== End Media Query mixin ====== //
/* ========= Start Global Rules =========*/

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

html {
  font-size: 100%;
  scroll-behavior: smooth;
}

body::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

body::-webkit-scrollbar-track {
  background-color: lightblue
}

body::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: lightblue;
}

body::-webkit-scrollbar-corner {
  border-radius: 10px;
  background-color:lightblue;
}

.container {
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;

  // === tablet screens === //
  @include breakpoint(tablet) {
    width: 750px;
  }

  // === laptop screens === //
  @include breakpoint(laptop) {
    width: 970px;
  }

  // === desktop screens === //
  @include breakpoint(desktop) {
    width: 1170px;
  }

  // === tv screens === //
  @include breakpoint(tv) {
    width: 1370px;
  }
}

video {
  height: auto;
  max-width: 100%;
}

img {
  height: auto;
  max-height: 100%;
  max-width: 100%;
}

ul {
  list-style: none;
}

::selection {
  color: #fff;
  background: var(--primary-clr);
}

/* ========= End Global Rules ===========*/

/* ====== Start Typography Rules ========*/

body {
  line-height: 1.5;
  overflow-x: hidden;
  min-height: 1000rem;
  font-family: var(--body-font);
  background-color: var(--background-clr);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
  font-family: var(--title-font);
}

h1 {
  font-size: 1.5rem;
  margin-bottom: .1875rem;

  @include breakpoint(desktop) {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

// h3 {
//   font-size: rem(24);
//   margin-bottom: rem(24);
// }

a,
a:visited,
a:active {
  text-decoration: none;
}

a:visited {
  color: lightblue;
}

/* ======= End Typography Rules =========*/
/* ========== Start Variables ===========*/

:root {
  /* === colors === */
  
  --white-clr: #fff;
  --black-clr: #000;
  
  --light-clr: #bababa;
  // --dark-clr: #2B2B2B;
  
  --foreground-clr: linear-gradient(to bottom, #dbd8d6 0%, #ededec 13%, #ededec 86%, #dbd8d6 99%, #dbd8d6 100%);
  --background-clr: #f5f5f5;

  --title-clr: #535362;
  --text-clr: #1b1b1b;
  
  --primary-clr: #13b0d7;
  --primary-alt-clr: #21688e7f;

  --danger-clr: #d20000;
  
  --secondary-clr: #9f2f0a;
  // --secondary-alt-clr: #7a2103;
  
  // --accent-clr: #e97900;
  // --accent-alt-clr: #eb4c03;

  /* === Family Fonts === */
  --body-font: "open Sans", sans-serif;
  --title-font: "Merriweather", sans-serif;

  /* === Box Shadows === */
  --box-shadow: 0px 0px 20px rgba(152, 152, 152, 0.33);

  /* === Line Height === */
  --line-height: 1.6;
  --line-height-lg: 2;

  /* === Transition === */
  --transition: 0.5s;

  /* === Border Radius === */
  --border-radius: 5px;
  --border-radius-lg: 10px;

  /* === Spaces === */
  --space-sm: 0.625rem;
  --space-md: 1.25rem;
  --space-lg: 3.125rem;
  --space-xlg: 6.25rem;

}

/* =========== End Variables ============*/
/* ========== Start light Theme =========*/
body.light {
  --white-clr: #fff;
  --black-clr: #000;

  --light-clr: #bababa;
  // --dark-clr: #2B2B2B;

  --foreground-clr: linear-gradient(to bottom, #dbd8d6 0%, #ededec 13%, #ededec 86%, #dbd8d6 99%, #dbd8d6 100%);
  --background-clr: #f5f5f5;

  --title-clr: #535362;
  --text-clr: #1b1b1b;

  --primary-clr: #13b0d7;
  --primary-alt-clr: #21688e7f;

  --danger-clr: #d20000;

  // --secondary-clr: #9f2f0a;
  // --secondary-alt-clr: #7a2103;

  // --accent-clr: #e97900;
  // --accent-alt-clr: #eb4c03;
}
/* =========== End light Theme ==========*/
/* ========== Start Dark Theme ==========*/
@media (prefers-color-scheme: dark) {
  :root {
    --white-clr: #000;
    --black-clr: #fff;

    --light-clr: #bababa;
    // --dark-clr: #2B2B2B;

    --foreground-clr: linear-gradient(to bottom, #2B2B2B 0%, #1F1F1F 13%, #1F1F1F 86%, #2B2B2B 99%, #2B2B2B 100%);
    --background-clr: #1F1F1F;

    --title-clr: #e3796a;
    --text-clr: #F0F4F5;

    --primary-clr: #0093b8;
    --primary-alt-clr: #21688e7f;

    --danger-clr: #d20000;

    // --secondary-clr: #9f2f0a;
    // --secondary-alt-clr: #7a2103;

    // --accent-clr: #e97900;
    // --accent-alt-clr: #eb4c03;

    img {
      filter: brightness(90%);
    }
  }
}

body.dark {

  --white-clr: #000;
  --black-clr: #fff;

  --light-clr: #bababa;
  // --dark-clr: #2B2B2B;

  --foreground-clr: linear-gradient(to bottom, #2B2B2B 0%, #1F1F1F 13%, #1F1F1F 86%, #2B2B2B 99%, #2B2B2B 100%);
  --background-clr: #1F1F1F;

  --title-clr: #e3796a;
  --text-clr: #F0F4F5;

  --primary-clr: #0093b8;
  --primary-alt-clr: #21688e7f;

  --danger-clr: #d20000;

  // --secondary-clr: #9f2f0a;
  // --secondary-alt-clr: #7a2103;

  // --accent-clr: #e97900;
  // --accent-alt-clr: #eb4c03;



  img {
    filter: brightness(90%);
  }
}

/* =========== End Dark Theme ===========*/
/* ======= Start common Styles ========= */

.title {
  position: relative;
  text-align: center;
  width: 200px;
  margin: 0 auto;

  &::before,
  &::after {
    content: '';
    position: absolute;
    display: block;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    bottom: -20px;
    border: 1px solid var(--light-clr);
    background-color: var(--light-clr)
  }

  &::before {
    left: -10px;
  }

  &::after {
    right: -10px;
  }

  h2 {
    position: relative;
    text-transform: uppercase;
    color: var(--title-clr);

    &::before {
      content: '';
      position: absolute;
      display: block;
      width: 100%;
      height: 2px;
      bottom: -17px;
      border-top: 1px solid var(--light-clr);
    }

    &::after {
      content: '';
      position: absolute;
      display: block;
      width: 25px;
      height: 25px;
      border-radius: 50%;
      left: 50%;
      bottom: -28px;
      transform: translateX(-50%);
      background-color: var(--light-clr);
    }
  }
  img {
    width: 30px;
    position: absolute;
    bottom: -28px;
    left: 50%;
    transform: translateX(-50%);
  }
}

/* ========= End common Styles ========= */
/* =========== Start Header ===========  */

header {
  .content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    height: 40px;
    border-bottom: 1px solid var(--primary-alt-clr);
    @include breakpoint(tablet) {
      height: 50px;
    }
    .logo {
      position: absolute;
      top: 45px;
      height: 40px;
      width: 40px;
      line-height: 35px;
      font-size: 22px;
      font-weight: 600;
      text-align: center;
      border-radius: 50%;
      user-select: none;
      color: var(--text-clr);
      text-transform: capitalize;
      border: 2px solid var(--primary-clr);
      border-left-style: dotted;
      @include breakpoint(tablet) {
        position: static;
      }
    }
    > ul {
      display: flex;
      align-items: center;
      justify-content: space-between;
      li {
        height: 30px;
        width: 30px;
      }
      li:not(:last-child) {
        margin-right: 10px;
        @include breakpoint(tablet) {
          margin-right: 40px;
        }
      }
    }
    .options {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    button {
      width: 30px;
      height: 30px;
      cursor: pointer;
      margin-right: 10px;
      img {
        width: 100%;
      }
    }
    .website__lang {
      position: relative;
      padding-left: 10px;
      cursor: pointer;
      z-index: 3;
      font-size: 20px;
      color: var(--text-clr);
      border-left: 1px solid var(--black-clr);
      img:first-child {
        width: 30px;
        padding-right: 3px;
      }
      .flag {
        position: relative;
        bottom: -3px;
      }
      &.open {
        .lang__list {
          display: block !important;
        }
        .arrow {
          transform: rotate(180deg);
        }
      }
    }
    .arrow {
      position: relative;
      top: 3px;
      width: 15px;
      transition: var(--transition);
    }
    .lang__list {
      display: none;
      position: absolute;
      top: 0;
      left: 0;
      transition: var(--transition);
      background: var(--foreground-clr);
      a {
        display: flex;
        align-items: center;
        justify-content: space-around;
        padding: 3px 10px;
        &:hover {
          background-color: var(--primary-alt-clr);
        }
      }
      span {
        color: var(--black-clr);
      }
    }
  }
  nav {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    position: relative;
    height: 50px;
    padding-block: 10px;
    @include breakpoint(tablet) {
      height: unset;
      max-width: 50rem;
      margin: 0 auto;
    }
    .toggle__manu {
      width: 35px;
      cursor: pointer;
      transition: var(--transition);
      transition-delay: 0.4s;
      @include breakpoint(tablet) {
          display: none;
        }
      span {
        display: block;
        height: 3px;
        width: 100%;
        transition: var(--transition);
        background-color: var(--primary-clr);
      }
      span:not(:last-child) {
        margin-bottom: 5px;
      }
    }
    .toggle__manu.open {
      transform: rotate(-45deg);
      span:first-child {
        transform: translateY(10px);
        transition-delay: 0.2s;
        background-color: var(--danger-clr);
      }
      span:nth-child(2) {
        transform: translateX(-15px);
        opacity: 0;
      }
      span:last-child {
        transform: rotate(-90deg) translateX(6px);
        transition-delay: 0.2s;
        background-color: var(--danger-clr);
      }
    }
    .toggle__manu.open ~ ul {
      opacity: 1 !important;
      z-index: 2 !important;
      bottom: -133px !important;
    }
    ul {
      display: grid;
      grid-template-columns: auto auto auto;
      position: absolute;
      bottom: -160px;
      width: 100%;
      opacity: 0;
      z-index: -2;
      transition: var(--transition);
      transition-delay: 0.2s;
      border-radius: 0 0 5px 5px;
      background: var(--foreground-clr);
      @include breakpoint(tablet) {
          display: flex;
          align-items: center;
          justify-content: space-around;
          position: static;
          opacity: 1;
          z-index: 2;
          background-color: transparent;
        }
      li {
        padding-block: 10px;
        border-radius: 5px;
        transition: var(--transition);
        &:hover {
          background-color: var(--primary-alt-clr);
        }
      }
      a {
        text-align: center;
        @include breakpoint(tablet) {
          display: flex;
          align-items: center;
          column-gap: 5px;
          padding-inline: 10px;
        }
        img {
          display: block; 
          width: 25px;
          margin: 0 auto;
          @include breakpoint(tablet) {
            display: inline;
            margin: unset;
          }
        }
        span {
          display: block;
          font-size: 14px;
          color: var(--text-clr);
          @include breakpoint(tablet) {
            display: inline;
            font-size: unset;
          }
        }
      }
    }
  }
}

/* =========== End Header =============  */
/* =========== Start Landing =========== */

.landing {
  min-height: 220px;
  background: var(--primary-clr) url(../assets/images/landing.jpg) top center / cover;
  @include breakpoint(tablet) {
      min-height: 550px;
  }
}
/* =========== End Landing ============= */
/* =========== Start About ============= */
.about {
  padding-block: 50px;
  background: var(--foreground-clr);
  .content {
    padding-block: 50px;
    @include breakpoint(phone) {
      display: flex;
      justify-content: space-between;
      column-gap: 20px;
    }
  }
  .img-holder {
    height: 400px;
    border-radius: 5px;
    background: var(--primary-clr) url(../assets/images/about.jpg) top center / cover;
  @include breakpoint(phone) {
    width: 50%;
  }
  }
  .filter {
    @include breakpoint(phone) {
      width: 50%;
    }
    ul {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-block: 15px;
      max-width: 300px;
      margin: 0 auto;
      li {
        font-weight: 600;
        padding: 10px 15px;
        border-radius: 5px;
        cursor: pointer;
        color: var(--black-clr);
        transition: var(--transition);
        border: 1px solid var(--primary-alt-clr);
      }
      li.active, li:hover, li:focus {
        color: var(--white-clr);
        border-color: var(--primary-alt-clr);
        background-color: var(--primary-alt-clr);
      }
    }
    .info {
      > div {
        display: none;
        margin-bottom: 20px;
        color: var(--text-clr);
        @include breakpoint(phone) {
          height: 275px;
        }
      }
      div.active {
        display: block;
      }
      
    }
    a {
      margin-top: 10px;
      padding: 5px 10px;
      border-radius: 5px;
      color: var(--text-clr);
      transition: var(--transition);
      color: var(--white-clr);
      background-color: var(--light-clr);
      &:hover, &:focus {
        background-color: var(--primary-alt-clr);
      }
      img {
        width: 15px;
        margin-left: 5px;
      }
    }
  }
}
/* ============ End About ============== */
/* =========== Start Skills ============ */

.skills {
  padding-block: 50px;
  .content {
    display: grid;
    grid-template-columns: auto auto;
    place-items: center;
    padding-top: 50px;
    gap: 30px;
    @include breakpoint(phone) {
      grid-template-columns: auto auto auto;
    }
    @include breakpoint(tablet) {
      grid-template-columns: auto auto auto auto;
    }
  }
  .skill {
    width: fit-content;
    text-align: center;
  }
  .progress {
    position: relative;
  }
  .percentage {
    position: absolute;
    top: 50%;
    left: 50%;
    color: white;
    transform: translate(-50%, -50%);
    span:first-child {
      font-weight: 600;
      font-size: 1.125rem;
      margin-right: 2px;
    }
    span:last-child {
      font-size: 10px;
    }
  }
  svg {
    width: 117px;
    height: 117px;
  }
  circle {
    fill: var(--primary-alt-clr);
    stroke-width: 0.5rem;
    transition: 3s;
    stroke-dasharray: 314.203369140625;
    &:first-child {
      stroke: var(--light-clr);
    }
    &:last-child {
      stroke: var(--primary-clr);
      stroke-dashoffset: 314;
    }
  }
}

/* ============ End Skills ============= */
